.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 50px;
    /*padding: 5px ;*/
    margin-bottom: 10px;
    /*border:1px  solid  #363636;*/


}

.header_title {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #088dcd;
    font-size: 22px;
    font-weight: 700;


}

.header_logo {
    width: 15%;
    display: flex;
    height: inherit;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    color: #088dcd;
}

.header_logo__button {
    color: #088dcd;
    background-color: inherit;
    cursor: pointer;
    padding: 2px 5px;
    border: 1px solid #088dcd;

}