.tasks_wrap {
    min-height: 600px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px 0;
    padding: 15px 0;
    overflow: auto;
    border:1px  solid  #363636;

    border-radius: 5px;
    background: #424242 none repeat scroll 0 0;

}
.tasks_input {
    width: 90%;
    display: flex;
    align-items: center;
    height: 40px;

}
.tasks_input input {
    width: 80%;
    padding: 0;
    margin: 0;
    padding-left: 20px;
    height: inherit;
    background-color: #393939;
    color: #ccc;
}


.tasks_input button {
    width: 20%;
    height: inherit;
    background-color: #088dcd;
    cursor: pointer;

}

/*===== анимация для кнопки добавдления task*/
@-webkit-keyframes hvr-push {
    50% {
        -webkit-transform: scale(0.8);
        transform: scale(0.8);
    }
    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}
@keyframes hvr-push {
    50% {
        -webkit-transform: scale(0.8);
        transform: scale(0.8);
    }
    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}
.tasks_button {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}
.tasks_button:active {
    -webkit-animation-name: hvr-push;
    animation-name: hvr-push;
    -webkit-animation-duration: 0.3s;
    animation-duration: 0.3s;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
}

/*=======*/
.tasks_item {
    width: 90%;
    height: 67px;
    margin: 10px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    border:1px  solid  #363636;
}
.task_title {
    width: 68%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: inherit;

}

/*===== анимация для поля title task */

.tasks_title__wrap {
    position: relative;
    display: flex;
    justify-content: center;
    width: 200px;
    padding: 20px 43px;
    text-decoration: none;
    text-transform: uppercase;
    overflow: hidden;
}
.tasks_title__wrap .task_title {
    position: relative;
    z-index: 1;
    color: #fff;
    width: 100%;
    font-size: 20px;
    letter-spacing: 8px;
    font-weight: bold;
    overflow: hidden;
}
.tasks_title__wrap .liquid{
    position: absolute;
    overflow: hidden;
    width: 100%;
    height: 200px;
    /*background: #5073ff;*/
    /*background: #ff0066;*/
    background: #33cc33;
    /*background: #ffcc00;*/
    top: -80px;
    left: 0;
    /*box-shadow: inset 0 0 50px rgba(0, 0, 0, 0.5);*/
    transition: 0.3s;
}
.tasks_title__wrap .liquid_red {
    background: #ff0066;
}
.tasks_title__wrap .liquid_green {
    background: #33cc33;
}
.tasks_title__wrap .liquid_yellow {
    background: #ffcc00;
}



.tasks_title__wrap .liquid:before, a .liquid:after{
    content: "";
    position: absolute;
    top: -72px;
    height: 356%;
    width: 268%;

    left: 50%;
    transform: translate(-50%, -75%);
}
.tasks_title__wrap .liquid::before{
    border-radius: 45%;
    background-color: rgba(66, 66, 66, 1);
    animation: float 8s linear infinite;
}
.tasks_title__wrap .liquid:after{
    border-radius: 40%;
    background: rgba(66, 66, 66, .5);
    animation: float 8s linear infinite;
}
@keyframes float {
    0%{
        transform: translate(-50%, -75%)rotate(0deg);
    }
    100%{
        transform: translate(-50%, -75%) rotate(360deg);
    }
}

/*=====*/
.task_buttons {
    width: 18%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: inherit;

}
.task_buttons button {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 31%;
    /*background-color:  #ffcc00;*/
    cursor: pointer;

    border-left: 1px solid #363636;

}
.task_button__red {
    background-color: #ff0066;
}
.task_button__green {
    background-color: #33cc33;
}
.task_button__yellow {
    background-color:  #ffcc00;
}

