.task_done {
    width: 8%;
    display: flex;
    align-items: center;
    height: inherit;
}

.task_done input {
    width: 100%;
}

.task_delete {
    width: 8%;
    display: flex;
    align-items: center;
    height: inherit;
}

.task_delete button {
    width: 100%;
    height: inherit;
    background-color: #088dcd;
    cursor: pointer;

}

/*===== анимация кнопки delete */

.task_delete button {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    background: #088dcd;
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
}
.task_delete button:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: red;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 50%;
    transform-origin: 50%;
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}
.task_delete button:hover, .task_delete button:focus, .task_delete button:active {
    color: white;
}
.task_delete button:hover:before, .task_delete button:focus:before, .task_delete button:active:before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}

